import React, {Component, createContext} from 'react';
 const Context = createContext();
 const userObj = JSON.parse(sessionStorage.getItem('userDetails')) || null;
 export class ContextUtilsStore extends Component {
    state = {isLoggedIn: false, userDetails: null, isNavigation: userObj && userObj.isRegulatoryAssessmentComplete || false};
    componentDidMount() {
        this._checkUserisLogin();
    }
    _checkUserisLogin = ()=>{
        const isSession = sessionStorage.getItem('userDetails') || false;
        isSession ? this.setState({userDetails: JSON.parse(isSession), isLoggedIn: true}) : this.setState({isLoggedIn: false});
    };

    _setUserDetails = (data) =>{
        sessionStorage.setItem("userDetails", JSON.stringify(data));
        this.setState({userDetails: data, isLoggedIn: true});
    };

    _logOut = ()=>{
        sessionStorage.removeItem("userDetails");
        this.setState({userDetails: null, isLoggedIn: false});
    }
    render(){
        return(
            <Context.Provider
                value={{...this.state, setContextUser: this._setUserDetails, logOut: this._logOut}}>
                {this.props.children}
            </Context.Provider>
        )
    }
 }
 export const UtilsConsumer = Context.Consumer;
 export default Context;