import React, { useContext, useState, useEffect } from 'react';
import {useNavigate} from "react-router-dom";
import UtilsContext from '../../Context/UtilsContext';
import {USERS} from '../../Services/Utils';
const Login = () => {
  const [isError, setIsError] = useState(false);
  const [isLoading, setIsLoading] = useState(false);
  const navigate = useNavigate();
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const context = useContext(UtilsContext);

  const errorMessage = () => {
    setTimeout(() => {
      setIsError(true);
      setIsLoading(false);
      return false;
    }, 2000);
  }

  useEffect(() => {
    context.logOut();
  }, [])

  const validateForm = () => {
    return email.length > 0 && password.length > 0;
  }

  const handleClick = (event) => {
    event.preventDefault();
    setIsError(false);
    setIsLoading(true);
    const obj = {
      username: email,
      password: password
    };

    // AppServices._userLogin(obj).then((res) => {
    //     if(res.httpStatus === '200') {
    //         redirectUser(res);
    //     } else {
    //         errorMessage();
    //     }
    // })

    let isValidated = USERS.find(item => item.email === email && item.password === password);
    console.log("isValidated",isValidated);
    if(isValidated){
        isValidated.password = undefined;
    }
    return (isValidated) ? redirectUser(isValidated) : errorMessage();
  }

  const redirectUser = (emailId) => {
    // AppServices._getUserDataById(emailId).then(res=>{
        setTimeout(() => {
            // MessageService.sendMessage(res);
            context.setContextUser(emailId);
            navigate("/dashboard");
        }, 2000);
    // })
}

  return (
    <div className="h-full gradient-form bg-gray-200 ">
      <div className="container py-5 px-6 min-h-[100vh] mx-auto flex justify-center items-center flex-wrap">
        <div className="flex justify-center items-center flex-wrap h-full g-6 text-gray-800">
          <div className="xl:w-10/12">
            <div className="block bg-white shadow-lg rounded-lg">
              <div className="lg:flex lg:flex-wrap g-0">
                <div className="lg:w-6/12 px-4 md:px-0">
                  <div className="md:p-12 md:mx-6">
                    <div className="text-center">
                      <img
                        className="mx-auto w-48"
                        src="/assets/images/logo.png"
                        alt=""
                      />
                      <h4 className="text-xl text-left font-semibold mt-12 pb-1">Sign In </h4>
                    </div>
                    <form>
                      <p className="mb-4">Please login to your account</p>
                      <div className="mb-4">
                        <input
                          type="text"
                          value={email} onChange={e => { setEmail(e.target.value); setIsError(false); }}
                          className="form-control block w-full px-3 py-1.5 text-base font-normal text-gray-700 bg-white bg-clip-padding border border-solid border-gray-300 rounded transition ease-in-out m-0 focus:text-gray-700 focus:bg-white focus:border-blue-600 focus:outline-none"
                          id="exampleFormControlInput1"
                          placeholder="Username"
                        />
                      </div>
                      <div className="mb-4">
                        <input
                          type="password"
                          value={password} onChange={e => { setPassword(e.target.value); setIsError(false); }}
                          className="form-control block w-full px-3 py-1.5 text-base font-normal text-gray-700 bg-white bg-clip-padding border border-solid border-gray-300 rounded transition ease-in-out m-0 focus:text-gray-700 focus:bg-white focus:border-blue-600 focus:outline-none"
                          id="exampleFormControlInput1"
                          placeholder="Password"
                        />
                      </div>
                      <div className="text-center pt-1 mb-12 pb-1">
                        <button
                          className="inline-block px-6 py-2.5 text-white font-medium text-xs leading-tight bg-blue-500 uppercase rounded shadow-md hover:bg-blue-700 hover:shadow-lg focus:shadow-lg focus:outline-none focus:ring-0 active:shadow-lg transition duration-150 ease-in-out w-full mb-3"
                          type="button"
                          data-mdb-ripple="true"
                          data-mdb-ripple-color="light"
                          onClick={(event) => handleClick(event)} disabled={!validateForm()}
                        >
                          Log in
                        </button>
                        <a className="text-gray-500" href="#!">Forgot password?</a>
                      </div>
                      {/* <div className="flex items-center justify-between pb-6">
                        <p className="mb-0 mr-2">Don't have an account?</p>
                        <button
                          type="button"
                          className="inline-block px-6 py-2 border-2 border-red-600 text-red-600 font-medium text-xs leading-tight uppercase rounded hover:bg-black hover:bg-opacity-5 focus:outline-none focus:ring-0 transition duration-150 ease-in-out"
                          data-mdb-ripple="true"
                          data-mdb-ripple-color="light"
                        >
                          Danger
                        </button>
                      </div> */}
                    </form>
                  </div>
                </div>
                <div
                  className="lg:w-6/12 flex items-center lg:rounded-r-lg rounded-b-lg lg:rounded-bl-none bg-[#ccc] bg-gradient-to-r from-slate-900 via-purple-900 to-slate-900"
                >
                  <div className="text-white px-4 py-6 md:p-12 md:mx-6">
                    <h4 className="text-xl font-semibold mb-6">Weclcome to Synechron - Finlabs</h4>
                    <p className="text-sm">
                      Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod
                      tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam,
                      quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo
                      consequat.
                    </p>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}

export default Login;