import { Button } from 'flowbite-react';
import React from 'react'
import { useNavigate } from 'react-router-dom';

const Welcome = () => {
  const navigate = useNavigate()
  return (
    <>
      <div className='p-9 bg-slate-500 text-md font-semibold text-center'>Welcome to Utils Application</div>
      <div className='flex gap-2 items-center mx-auto justify-center pt-[30px]'>  <Button onClick={() => navigate("/login")}>Login</Button>
        <Button onClick={() => navigate("/dashboard")}>Dashboard</Button></div>
    </>

  )
}

export default Welcome;